import React , { useState , useEffect } from 'react';


const Sayac = () => {

    const [sayac,setSayac] = useState(0);

    useEffect(() => {
        document.title = sayac;  //"${sayac}"
    })
    
    return(
        <div>
            <button onClick={()=> setSayac(sayac + 1)}>{sayac}</button>
        </div>
    );

}


export default Sayac;